<template>
  <div>
    <header class="mui-bar mui-bar-nav">
      <button class="mui-btn mui-btn-blue mui-btn-link mui-btn-nav mui-pull-left" @click="goBack">
        <span class="mui-icon mui-icon-left-nav"></span>返回
      </button>
      <h1 class="mui-title">用户验证</h1>
    </header>
    <div class="mui-content" style="margin-top:3px;">
      <form class="mui-input-group">
        <div class="mui-input-row">
          <label>手机号码</label>
          <input
            type="text"
            class="mui-input-clear"
            v-model="userPhones"
            readonly
          >
        </div>
        <div class="mui-input-row">
          <label>验证码</label>
          <input type="text" id="tel" class="mui-input-clear" placeholder="请输入验证码" v-model="codeMsg">
          <button
            type="button"
            id="vercode"
            class="mui-btn mui-btn-primary mui-btn-outlined"
            @click="getImageMessage"
          >{{countDown}}</button>
        </div>
        <div class="mui-button-row">
          <button
            type="button"
            data-loading-icon="mui-spinner mui-spinner-custom"
            class="mui-btn mui-btn-primary"
            @click="checkedMessage"
          >确定</button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  name: "Identifying",
  data() {
    return {
      userPhones: "",
      codeMsg: "",
      userCode: "",
      userTypeCode: "",
      userLoginCode: "",
      isTestIndex: true,
      userByCode: "",
      countDown:'获取验证码',
      timeCount: 60,
      canClick:true
    };
  },
  methods: {
    goBack: function() {
      window.history.go(-1);
    },
    getImageMessage() {
      if(!this.canClick) return
      if (this.userPhones) {
        this.$http
          .post("/sendVercode", {
            userCode: this.userCode,
            userPhones: this.userPhones
          })
          .then(
            response => {
              let type = response.data.status;
              console.log(type);
              if (type) {
                if (type == "success") {
                  console.log("成功");
                  this.userByCode = response.data.userByCode;
                } else {
                  alert(type);
                }
              }
            },
            error => {
              console.log(error);
            }
          );
      }
      this.canClick = false
      this.countDown = this.timeCount+'s后重新发送'
      let clock = window.setInterval(() => {
        this.timeCount--
        this.countDown = this.timeCount + 's后重新发送'
        if(this.timeCount < 0){
          window.clearInterval(clock)
          this.countDown = '获取验证码'
          this.timeCount = 60
          this.canClick =  true
        }
      },1000)
    },
    checkedMessage() {
      if (!this.userByCode) {
        alert("请获取验证码");
        return;
      }
      if (this.codeMsg) {
        console.log(this.userCode);
        this.$http
          .post("/checkedMessage", {
            userCode: this.userCode,
            userTypeCode: this.userTypeCode,
            userLoginCode: this.userLoginCode,
            userByCode: this.userByCode,
            codeMsg: this.codeMsg,
            userPhones: this.userPhones
          })
          .then(
            response => {
              let type = response.data.status;
              if (type) {
                if (type == "success") {
                  this.$router.push("/Home");
                } else {
                  alert(response.data.msg);
                }
              }
            },
            error => {
              console.log(error);
            }
          );
      } else {
        alert("请输入验证码");
      }
    },
    isDefault(field) {
      return this.isTestIndex;
    }
  },
  created: function() {
    this.userPhones = this.$store.getters.user.userPhones;
    this.userCode = this.$store.getters.user.userCode;
    this.userTypeCode = this.$store.getters.user.userTypeCode;
    this.userLoginCode = this.$store.getters.user.userLoginCode;
  }
};
</script>
<style scoped>
#tel{
  width:32%;
  float: left;
}
#vercode{
  float: left;
  width: 30%;
  /* margin-top: 7.3px; */
  border: none;
}
</style>



